/**
 * Test en temps rel pour Google Analytics
 * Permet de vrifier si les vnements sont envoys immdiatement
 */

import { analytics, EventCategory, EventAction } from '../services/firebaseAnalytics';

/**
 * Teste l'envoi d'vnements en temps rel
 */
export const testAnalyticsRealTime = () => {
  console.group(' Test Google Analytics Temps Rel');
  
  const timestamp = new Date().toISOString();
  const testId = Math.random().toString(36).substring(7);
  
  console.log(` Envoi d'vnements de test (ID: ${testId})`);
  console.log(` Timestamp: ${timestamp}`);
  
  // Test 1: Page view
  try {
    analytics.trackPageView('/test-realtime', `Test Temps Rel ${testId}`);
    console.log(' 1. Page view envoy');
  } catch (error) {
    console.error(' 1. Erreur page view:', error);
  }
  
  // Test 2: Interaction utilisateur
  try {
    analytics.trackInteraction(EventAction.CLICK, 'test-realtime-button', {
      test_id: testId,
      timestamp: timestamp,
      realtime_test: true
    });
    console.log(' 2. Interaction envoye');
  } catch (error) {
    console.error(' 2. Erreur interaction:', error);
  }
  
  // Test 3: vnement personnalis
  try {
    analytics.trackEvent(EventCategory.FEATURE, EventAction.FEATURE_USE, {
      feature_name: 'realtime_analytics_test',
      test_id: testId,
      timestamp: timestamp,
      user_agent: navigator.userAgent.substring(0, 50)
    });
    console.log(' 3. vnement personnalis envoy');
  } catch (error) {
    console.error(' 3. Erreur vnement personnalis:', error);
  }
  
  // Test 4: vnement communautaire
  try {
    analytics.trackCommunityInteraction(EventAction.VIEW, {
      content_type: 'realtime_test',
      test_id: testId,
      timestamp: timestamp
    });
    console.log(' 4. vnement communautaire envoy');
  } catch (error) {
    console.error(' 4. Erreur vnement communautaire:', error);
  }
  
  console.log('');
  console.log(' Pour vrifier en temps rel:');
  console.log('1. Ouvrez Google Analytics 4');
  console.log('2. Allez dans Rapports > Temps rel');
  console.log(`3. Cherchez les vnements avec test_id: ${testId}`);
  console.log('4. Les vnements devraient apparatre dans les 10-30 secondes');
  
  console.groupEnd();
  
  return testId;
};

/**
 * Lance une srie de tests espacs dans le temps
 */
export const testAnalyticsRealTimeSequence = () => {
  console.log(' Dmarrage de la squence de tests temps rel...');
  
  // Test immdiat
  const testId1 = testAnalyticsRealTime();
  
  // Test aprs 10 secondes
  setTimeout(() => {
    console.log(' Test aprs 10 secondes...');
    testAnalyticsRealTime();
  }, 10000);
  
  // Test aprs 30 secondes
  setTimeout(() => {
    console.log(' Test aprs 30 secondes...');
    testAnalyticsRealTime();
  }, 30000);
  
  // Test aprs 1 minute
  setTimeout(() => {
    console.log(' Test aprs 1 minute...');
    testAnalyticsRealTime();
  }, 60000);
  
  return testId1;
};

/**
 * Expose les fonctions de test dans la console pour utilisation manuelle
 */
if (typeof window !== 'undefined') {
  (window as any).testAnalyticsRealTime = testAnalyticsRealTime;
  (window as any).testAnalyticsRealTimeSequence = testAnalyticsRealTimeSequence;
  
  console.log(' Fonctions de test Analytics disponibles:');
  console.log('- testAnalyticsRealTime() : Test unique');
  console.log('- testAnalyticsRealTimeSequence() : Squence de tests');
}
